          <blockquote style="margin-top:-20px" class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Barry Allen的经典故事介绍了Multiverse to DC Comics的概念，这个概念在DC 多年来的各种连续性重启中发挥了重要作用。Flash一直在DC的主要公司重启故事中发挥着重要作用，而在无限地球的交叉危机＃8（1985年11月）中，Barry Allen死于拯救多重宇宙，从常规DC阵容中移除角色23年。在格兰特莫里森的交叉故事“ 最后的危机：盗贼的复仇＃3”（2008年11月）的叙述中（以及模糊的单一图像），他回归常规漫画，在Geoff Johns的陪同下完全实现了Flash：Rebirth＃1（2009年6月），开启了六期限量系列。此后，他在 Blackest Night（2009）， Flashpoint（2011）， Convergence（2015）和 DC Rebirth（2016）等交叉故事中发挥了关键作用。</blockquote>
        
