var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("闪电侠")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        闪电侠（缪亨利·阿伦）是一个超级英雄出现在美国漫画书的出版DC漫画。这个角色最初出现在Showcase＃4（1956年10月），由作家Robert Kanigher和penciler Carmine Infantino创作。巴里艾伦改编了一个名为Flash的角色，他出现在20世纪40年代的漫画书作为角色杰伊加里克。他的力量主要包括超人的速度。各种其他效果也归因于他控制分子振动速度的能力，包括他以高速振动以穿过物体的能力。闪光服饰有独特的红色和金色服装，经过处理以抵抗摩擦和风阻，传统上将服装压缩在戒指内。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Barry Allen的经典故事介绍了Multiverse to DC Comics的概念，这个概念在DC 多年来的各种连续性重启中发挥了重要作用。Flash一直在DC的主要公司重启故事中发挥着重要作用，而在无限地球的交叉危机＃8（1985年11月）中，Barry Allen死于拯救多重宇宙，从常规DC阵容中移除角色23年。在格兰特莫里森的交叉故事“ 最后的危机：盗贼的复仇＃3”（2008年11月）的叙述中（以及模糊的单一图像），他回归常规漫画，在Geoff Johns的陪同下完全实现了Flash：Rebirth＃1（2009年6月），开启了六期限量系列。此后，他在 Blackest Night（2009）， Flashpoint（2011）， Convergence（2015）和 DC Rebirth（2016）等交叉故事中发挥了关键作用。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        巴里艾伦是一名警察化学家（他的工作头衔改为“闪电：铁高地一次性” 的法医化学家），其声誉非常缓慢，而且经常迟到，这使他的未婚妻Iris West感到沮丧，因为心不在焉，他对解决犯罪的热爱。一天晚上，在他工作到很晚的时候，闪电击碎了一个装满化学品的箱子，并在巴里各处洒落。结果，艾伦发现他可以跑得非常快，并且具有匹配的反应和感官。他戴着一套带有闪电的红色紧身衣（让人想起原来的Fawcett Comics Captain Marvel），将自己称为Flash（在童年漫画英雄杰伊加里克之后）），并成为中央城市的常驻打扮的犯罪战士。中央城市大学教授Ira West（Iris的养父）设计了艾伦的服装和存放它的戒指，而艾伦则是他的平民身份。当艾伦需要时，戒指可以弹出压缩的衣服，并借助于缩小衣服的特殊气体将其吸回。此外，艾伦发明了宇宙跑步机，这种设备可以实现精确的时间旅行，并被用于许多故事中。艾伦非常受欢迎，几乎所有追随他的超速驾驶者都经常与他相提并论。蝙蝠侠曾经说过“如果我的父母没有被谋杀，巴里就是那种我希望成为的男人”。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-im